  display: flex;
  justify-content: center;
  border-radius: 20px 20px 0 0;
  height: 250px;
  background-color: #fafafa;
}
.product-card__photo img {
  margin-top: -50px;
}
.product-card__descr {
  display: flex;
  flex-direction: column;
  border-radius: 0 0 20px 20px;
  padding: 14px 22px 28px;
  min-height: 202px;
  background-color: var(--light-color);
  gap: 6px;
}
.product-card__category {
  z-index: 5;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 16px;
  color: var(--text-muted);
}
.product-card__heading {
  margin: 0;
}
.product-card__link {
  font-weight: 600;
  font-size: 21px;
  text-transform: capitalize;
  color: var(--text-card-dark-blue);
}
.product-card__link::after {
  content: "";
  position: absolute;
  inset: 0;
}
.product-card__reiting {
  display: flex;
  gap: 3px;
}
.product-card__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
}
.product-card__price {
  display: flex;
  align-items: flex-start;
  font-weight: 600;
  font-size: 21px;
  color: var(--text-card-dark-blue);
  gap: 3px;
}
.product-card__price::before {
  content: attr(data-value);
  font-size: 15px;
}
.product-card__btn {
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 31px;
  width: 48px;
  height: 48px;
  background-color: var(--text-card-dark-blue);
  transition: opacity 0.3s ease-in-out;
}
.product-card__btn:hover {
  opacity: 0.8;
}
.product-card__btn--icon {
  width: 24px;
  height: 24px;
  fill: var(--light-color);
}

.footer {
  padding: 118px 0 56px;
  background-color: #2d2f33;
}
.footer__grid {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  margin-bottom: 118px;
  gap: 20px;
}
@media (max-width: 768px) {
  .footer__grid {
    margin-bottom: 50px;
  }
}
.footer__logo {
  grid-column: 3 span;
}
.footer__logo img {
  margin-bottom: 29px;
}
@media (max-width: 576px) {
  .footer__logo img {
    margin: 0 auto;
    margin-bottom: 20px;
  }
}
@media (max-width: 768px) {
  .footer__logo {
    grid-column: 10 span;
    margin-bottom: 20px;
    text-align: center;
  }
}
.footer__descr {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 160%;
  color: #fff;
  opacity: 0.8;
}
.footer__nav {
  grid-column: 2 span;
}
.footer__nav--services {
  grid-column: 5/span 2;
}
@media (max-width: 768px) {
  .footer__nav--services {
    grid-column: 2/5 span;
  }
}
.footer__nav-title {
  margin: 0;
  margin-bottom: 20px;
  font-family: var(--font4);
  font-weight: 400;
  font-size: 17px;
  letter-spacing: -0.01em;
  color: #f6973f;
}
.footer__item {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.footer__item a {
  font-weight: 400;
  font-size: 15px;
  line-height: 160%;
  color: var(--light-color);
  opacity: 0.8;
  transition: opacity 0.3s ease-in-out;
}
.footer__item a:hover {
  opacity: 0.6;
}
.footer__row {
  display: flex;
  justify-content: space-between;
  color: var(--light-color);
}
@media (max-width: 576px) {
  .footer__row {
    flex-direction: column;
  }
}
.footer__copyright {
  font-family: var(--font4);
  font-weight: 400;
  font-size: 15px;
  letter-spacing: -0.01em;
  color: #fff;
  opacity: 0.8;
}
@media (max-width: 576px) {
  .footer__copyright {
    margin-bottom: 20px;
    text-align: center;
  }
}
.footer__links {
  display: flex;
  align-items: center;
  gap: 40px;
}
@media (max-width: 576px) {
  .footer__links {
    flex-direction: column;
    gap: 20px;
  }
}
.footer__link {
  font-weight: 400;
  font-size: 15px;
  color: var(--light-color);
  opacity: 0.8;
  transition: opacity 0.3s ease-in-out;
  gap: 40px;
}
.footer__link:hover {
  opacity: 0.5;
}
@media (max-width: 768px) {
  .footer {
    padding: 50px 0;
  }
}